@import '../vars/vars.telenet';
@import '../badge.default';

:host([background='dark-brown']) {
  background-color: $badge--default-bg;
  color: $badge--default-color;
}
:host([background='white']) {
  background-color: var(--semantic-color-background-surface-highlight-8);
  color: var(--semantic-color-text-default);
}
:host([background='yellow']) {
  background-color: var(--semantic-color-background-canvas-brand);
  color: var(--semantic-color-text-default);
}
:host([background='lemon']) {
  background-color: var(--semantic-color-background-canvas-accent);
  color: var(--semantic-color-text-default);
}
:host([background='bright-blue']) {
  background-color: var(--semantic-color-background-surface-highlight-1);
  color: var(--semantic-color-text-default);
}
:host([background='jeans-blue']) {
  background-color: var(--semantic-color-background-surface-highlight-4);
  color: var(--semantic-color-text-default);
}
:host([background='lila']) {
  background-color: var(--semantic-color-background-surface-highlight-2);
  color: var(--semantic-color-text-default);
}
:host([background='olivine']) {
  background-color: var(--semantic-color-background-surface-highlight-6);
  color: var(--semantic-color-text-default);
}
:host([background='turquoise']) {
  background-color: var(--semantic-color-background-surface-highlight-4);
  color: var(--semantic-color-text-default);
}
:host([background='watermelon']) {
  background-color: var(--semantic-color-background-surface-highlight-3);
  color: var(--semantic-color-text-default);
}
